import React, { useContext } from 'react';
import {
  Box,
  Typography,
  List,
  ListItem,
  ListItemText,
  AppBar,
  Toolbar,
  IconButton,
} from '@mui/material';
import { DataContext } from './DataContext';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useNavigate } from 'react-router-dom';

function AnalysisPage() {
  const { data } = useContext(DataContext); // Obtener los datos del contexto
  const navigate = useNavigate();

  const handleGoBack = () => {
    navigate('/');
  };

  // Obtener los nombres de los datos
  const nombres = data.map((item, index) => ({
    id: index,
    nombre: item['Nombre'], // Asegúrate de que el nombre de la columna coincide exactamente
  }));

  return (
    <Box sx={{ p: 2 }}>
      {/* Barra superior con botón de volver */}
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={handleGoBack}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6">Análisis de Nombres</Typography>
        </Toolbar>
      </AppBar>

      {/* Contenido principal */}
      <Typography variant="h4" sx={{ mt: 2, mb: 2 }}>
        Lista de Nombres
      </Typography>
      <List>
        {nombres.map((item) => (
          <ListItem key={item.id}>
            <ListItemText primary={item.nombre} />
          </ListItem>
        ))}
      </List>
    </Box>
  );
}

export default AnalysisPage;